import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  cancelOrder,
  createOrder,
  createOrderWithCard,
  fetchOrdersByUser,
  validateOrderWithCard,
} from "./orderAPI";

const initialState = {
  orders: [],
  currentOrder: null,
  orderCardPaymentData: null,
  orderLoaded: false,
  status: "idle",
};

export const createOrderAsync = createAsyncThunk(
  "order/createOrder",
  async (order) => {
    const response = await createOrder(order);
    return response.data;
  }
);
export const createOrderWithCardAsync = createAsyncThunk(
  "order/createOrderWithCard",
  async (order) => {
    const response = await createOrderWithCard(order);
    return response.data;
  }
);
export const validateOrderWithCardAsync = createAsyncThunk(
  "order/validateOrderWithCard",
  async (order) => {
    const response = await validateOrderWithCard(order);
    return response.data;
  }
);
export const fetchOrdersByUserAsync = createAsyncThunk(
  "order/fetchOrdersByUser",
  async () => {
    const response = await fetchOrdersByUser();
    return response.data;
  }
);
export const cancelOrderAsync = createAsyncThunk(
  "order/cancelOrder",
  async (id) => {
    const response = await cancelOrder(id);
    return response.data;
  }
);

export const orderSlice = createSlice({
  name: "order",
  initialState,

  reducers: {
    resetOrder: (state, action) => {
      state.currentOrder = null;
    },
    resetOrderCardPaymentData: (state, action) => {
      state.orderCardPaymentData = null;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(fetchOrdersByUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchOrdersByUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.orders = action.payload;
        state.orderLoaded = true;
      })
      .addCase(createOrderAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createOrderAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.orders.push(action.payload);
        state.currentOrder = action.payload;
      })
      .addCase(createOrderWithCardAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createOrderWithCardAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.orderCardPaymentData = action.payload;
      })
      .addCase(cancelOrderAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(cancelOrderAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.orders.findIndex(
          (order) => order.id === action.payload.id
        );

        state.orders.splice(index, 1);
      });
  },
});

export const { resetOrder, resetOrderCardPaymentData } = orderSlice.actions;

export const selectOrders = (state) => state.order.orders;
export const selectCurrentOrder = (state) => state.order.currentOrder;
export const selectOrderStatus = (state) => state.order.status;
export const selectOrderLoaded = (state) => state.order.orderLoaded;
export const selectOrderCardPaymentData = (state) =>
  state.order.orderCardPaymentData;
export default orderSlice.reducer;
