export function createReview(review) {
  return new Promise(async (resolve, reject) => {
    const response = await fetch("/reviews/createReview", {
      method: "POST",
      body: JSON.stringify(review),
      headers: { "content-type": "application/json" },
    });
    if (response.ok) {
      const data = await response.json();
      resolve({ data });
    } else {
      const error = await response.json();
      reject({ error });
    }
  });
}
export function fetchReviewByProduct(id) {
  return new Promise(async (resolve) => {
    const response = await fetch("/reviews/fetchReviewByProduct/" + id);
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchReviewByUser() {
  return new Promise(async (resolve) => {
    const response = await fetch("/reviews/fetchReviewByUser");
    const data = await response.json();
    resolve({ data });
  });
}
