import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchCount } from "./commonAPI";

const initialState = {
  value: 0,
  filter: {},
  sort: {},
  status: "idle",
};

export const incrementAsync = createAsyncThunk(
  "common/fetchCount",
  async (amount) => {
    const response = await fetchCount(amount);

    return response.data;
  }
);

export const commonSlice = createSlice({
  name: "common",
  initialState,

  reducers: {
    setFilterSync: (state, action) => {
      state.filter = action.payload;
    },
    setSortSync: (state, action) => {
      state.sort = action.payload;
    },

    incrementByAmount: (state, action) => {
      state.value += action.payload;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(incrementAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(incrementAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.value += action.payload;
      });
  },
});

export const { setFilterSync, setSortSync, incrementByAmount } =
  commonSlice.actions;

export const selectCount = (state) => state.common.value;
export const selectFilter = (state) => state.common.filter;
export const selectSort = (state) => state.common.sort;

export const incrementIfOdd = (amount) => (dispatch, getState) => {
  const currentValue = selectCount(getState());
  if (currentValue % 2 === 1) {
    dispatch(incrementByAmount(amount));
  }
};

export default commonSlice.reducer;
