import React from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import logo from "../assets/favicon.ico";
import {
  resetPasswordAsync,
  selectPasswordReset,
  selectPasswordResetError,
} from "../features/auth/authSlice";
const ResetPasswordPage = () => {
  const query = new URLSearchParams(window.location.search);
  const token = query.get("token");
  const email = query.get("email");
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const resetPasswordError = useSelector(selectPasswordResetError);
  const passwordReset = useSelector(selectPasswordReset);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  return (
    <>
      {email && token ? (
        <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-12 lg:px-8">
          <div className="sm:mx-auto sm:w-full sm:max-w-sm">
            <img className="mx-auto w-16" src={logo} alt="Your Company" />
            <h2 className="mt-2 text-center text-xl leading-1 tracking-tighter font-bolder text-gray-900 uppercase font-Oswald">
              Enter New Password
            </h2>
          </div>

          <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
            <form
              noValidate
              onSubmit={handleSubmit((data) => {
                console.log(data);
                dispatch(
                  resetPasswordAsync({ email, token, password: data.password })
                );
              })}
              className="space-y-6"
            >
              <div>
                <div className="flex items-center justify-between">
                  <label
                    htmlFor="password"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    New Password
                  </label>
                </div>
                <div className="mt-2">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    {...register("password", {
                      required: "Password required",
                      pattern: {
                        value:
                          /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm,
                        message: `- at least 8 characters
                      - must contain at least 1 uppercase letter, 1 lowercase letter, and 1 number
                      - Can contain special characters`,
                      },
                    })}
                    placeholder="Password"
                    autoComplete="current-password"
                    required
                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-black sm:text-sm sm:leading-6"
                  />
                  {errors.password && (
                    <p className="text-red-500">{errors.password.message}</p>
                  )}
                </div>
              </div>

              <div>
                <div className="flex items-center justify-between">
                  <label
                    htmlFor="password"
                    className="block text-sm font-medium leading-6 text-gray-900"
                  >
                    Confirm Password
                  </label>
                </div>
                <div className="mt-2">
                  <input
                    id="password"
                    name="password"
                    type="password"
                    {...register("confirmPassword", {
                      required: "Confirm password is required",
                      validate: (value, formsValues) =>
                        value === formsValues.password ||
                        "password not matching",
                    })}
                    autoComplete="current-password"
                    placeholder="Confirm Password"
                    required
                    className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-black sm:text-sm sm:leading-6"
                  />
                  {errors.confirmPassword && (
                    <p className="text-red-500">
                      {errors.confirmPassword.message}
                    </p>
                  )}
                  {passwordReset && alert("Password Reset successfully")}
                  {passwordReset && navigate("/login")}
                  {resetPasswordError && (
                    <p className="text-red-500">{resetPasswordError}</p>
                  )}
                </div>
              </div>
              <div>
                <button
                  type="submit"
                  className="flex uppercase font-Oswald tracking-tighter w-full justify-center rounded-sm bg-black px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm  focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
                  Reset Password
                </button>
              </div>
            </form>

            <p className="mt-4 text-center text-sm text-gray-500">
              Or return to
              <Link
                to="/login"
                className="font-semibold leading-6 text-blackunderline underline"
              >
                Login
              </Link>
            </p>
          </div>
        </div>
      ) : (
        <p>Invalid Link</p>
      )}
    </>
  );
};

export default ResetPasswordPage;
