import React, { useEffect } from "react";
import "./App.css";
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom";
import LandingPage from "./pages/LandingPage";
import Home from "./pages/Home";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchCategoriesAsync,
  fetchColorsAsync,
  fetchSizesAsync,
} from "./features/product/productSlice";

import AdminProductFormPage from "./pages/admin/AdminProductFormPage";
import ProductOverviewPage from "./pages/ProductOverviewPage";
import SignupPage from "./pages/SignupPage";
import LoginPage from "./pages/LoginPage";
import { checkUserAsync, selectLogginUser } from "./features/auth/authSlice";
import AdminHome from "./pages/admin/AdminHome";
import ShoppingCartPage from "./pages/ShoppingCartPage";
import { fetchCartByUserAsync } from "./features/cart/cartSlice";
import { fetchfavouriteByUserAsync } from "./features/favourite/favouriteSlice";
import ShoppingFavouritePage from "./pages/ShoppingFavouritePage";
import CheckoutPage from "./pages/CheckoutPage";
import { fetchUserByIdAsync } from "./features/user/userSlice";
import OrderSuccess from "./components/OrderSuccess";
import PageNotFound from "./components/PageNotFound";
import CheckoutCardPayment from "./containers/CheckoutCardPayment";
import DropDownSearchBar from "./components/DropDownSearchBar";
import ForgetPassword from "./pages/ForgetPasswordPage";
import ResetPasswordPage from "./pages/ResetPasswordPage";
import UserOrderPage from "./pages/UserOrderPage";
import { fetchOrdersByUserAsync } from "./features/order/orderSlice";
import UserProfilePage from "./pages/UserProfilePage";
import ProtectedAdmin from "./authentication/ProtectedAdmin";
import ProtectedUser from "./authentication/ProtectedUser";
const router = createBrowserRouter([
  {
    path: "/",
    element: <LandingPage></LandingPage>,
  },
  {
    path: "/home",
    element: <Home></Home>,
  },
  {
    path: "/productOverview/:id",
    element: <ProductOverviewPage></ProductOverviewPage>,
  },
  {
    path: "/shoppingCart",
    element: (
      <ProtectedUser>
        <ShoppingCartPage></ShoppingCartPage>
      </ProtectedUser>
    ),
  },
  {
    path: "/shoppingFavourite",
    element: (
      <ProtectedUser>
        <ShoppingFavouritePage></ShoppingFavouritePage>
      </ProtectedUser>
    ),
  },
  {
    path: "/userOrders",
    element: (
      <ProtectedUser>
        <UserOrderPage></UserOrderPage>
      </ProtectedUser>
    ),
  },
  {
    path: "/userProfile",
    element: (
      <ProtectedUser>
        <UserProfilePage></UserProfilePage>
      </ProtectedUser>
    ),
  },
  {
    path: "/checkout",
    element: (
      <ProtectedUser>
        <CheckoutPage></CheckoutPage>
      </ProtectedUser>
    ),
  },
  {
    path: "/orderSuccess/:id",
    element: (
      <ProtectedUser>
        <OrderSuccess></OrderSuccess>
      </ProtectedUser>
    ),
  },
  {
    path: "/checkoutCardPayment",
    element: (
      <ProtectedUser>
        <CheckoutCardPayment></CheckoutCardPayment>
      </ProtectedUser>
    ),
  },
  {
    path: "/admin/productForm",
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: "/admin/productForm/:id",
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: "/admin/home",
    element: (
      <ProtectedAdmin>
        <AdminHome></AdminHome>
      </ProtectedAdmin>
    ),
  },
  {
    path: "/signup",
    element: <SignupPage></SignupPage>,
  },
  {
    path: "/login",
    element: <LoginPage></LoginPage>,
  },
  {
    path: "/forgotPassword",
    element: <ForgetPassword></ForgetPassword>,
  },
  {
    path: "/resetPassword",
    element: <ResetPasswordPage></ResetPasswordPage>,
  },
  {
    path: "*",
    element: <PageNotFound></PageNotFound>,
  },
]);
function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLogginUser);
  useEffect(() => {
    dispatch(checkUserAsync());
  }, [dispatch]);

  useEffect(() => {
    dispatch(fetchCategoriesAsync());
    dispatch(fetchColorsAsync());
    dispatch(fetchSizesAsync());
  }, [dispatch]);
  useEffect(() => {
    if (user) {
      dispatch(fetchCartByUserAsync());
      dispatch(fetchfavouriteByUserAsync());
      dispatch(fetchUserByIdAsync());
      dispatch(fetchOrdersByUserAsync());
    }
  }, [dispatch, user]);
  return (
    <div className="App">
      <RouterProvider router={router} />
      {/* <DropDownSearchBar></DropDownSearchBar> */}
    </div>
  );
}

export default App;
