import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { Link, useLocation, useParams } from "react-router-dom";
import { resetCartAsync } from "../features/cart/cartSlice";
import {
  resetOrder,
  resetOrderCardPaymentData,
} from "../features/order/orderSlice";
import Swal from "sweetalert2";
export default function OrderSuccess() {
  const params = useParams();
  const dispatch = useDispatch();
  const location = useLocation();
  useEffect(() => {
    dispatch(resetCartAsync());
    dispatch(resetOrder());
    dispatch(resetOrderCardPaymentData());
  }, [dispatch]);
  useEffect(() => {
    console.log(location);
    // Swal.close();
    Swal.clickConfirm();
  }, [location]);
  return (
    <>
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-base font-semibold text-black">
            {" "}
            Order Successfully Placed
          </p>
          <h1 className="mt-4 text-xl md:text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            Order Number #{params.id}
          </h1>
          <p className="mt-6 text-base leading-7 text-gray-600">
            You can check your order in My Account &gt; My Orders
          </p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <Link
              to="/home"
              className="rounded-md bg-black px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:opacity-80 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 "
            >
              Go back home
            </Link>
            {/* <a href="#" className="text-sm font-semibold text-gray-900">
              Contact support <span aria-hidden="true">&rarr;</span>
            </a> */}
          </div>
        </div>
      </main>
    </>
  );
}
